.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.0rem;
}
.nav-item {
    display: flex;
    align-items: center;
    height: 80px;
    margin: 5px;
}
.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.7rem 1rem;
}
.nav-links:hover {
    background-color: #1888ff;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}