.Button_button__3X29B {    
    background: var(--primary-btn-color);
    color: #fff;
    padding: 15px 30px;
    position: fixed;
    top: 60%;
    left: 45%;
    border: 2px solid transparent;
    border-radius: 10px;
    -webkit-animation: Button_up__8EiOE 0.5s ease-in;
            animation: Button_up__8EiOE 0.5s ease-in;
    text-transform: uppercase;
    font-size: 0.8em;
    letter-spacing: 0.1em;    
}

.Button_button__3X29B:hover{    
    background: var(--secondary-btn-color);
    color: var(--primary-btn-color);
    box-shadow: 0 0 35px var(--secondary-btn-color);
}
.Button_button__3X29B::before{
    position: absolute;
    inset: 2px;    
}
.Button_button__3X29B span{
    position: relative;
    z-index: 1;
}
.Button_button__3X29B i{
    position: absolute;
    inset: 0;
    display: block;
}
.Button_button__3X29B i::before{
    content: '';
    position: absolute;
    border: 2px solid var(--primary-btn-color);
    width: 10px;
    height: 5px;
    left: 80%;
    top: -3.5px;
    /* background: #27282c; */
    background: var(--terciary-btn-color);
    transition: 0.5s;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.Button_button__3X29B:hover i::before{
    width: 20px;
    left: 20%;
}

.Button_button__3X29B i::after{
    content: '';
    position: absolute;    
    border: 2px solid var(--primary-btn-color);
    width: 10px;
    height: 5px;
    left: 20%;
    bottom: -3.5px;
    /* background: #27282c; */
    background: var(--secondary-btn-color);
    transition: 0.5s;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.Button_button__3X29B:hover i::after{
    width: 20px;
    left: 80%;
}

@-webkit-keyframes Button_up__8EiOE {
    0% {
        opacity: 0;;
        -webkit-transform: translateY(25px);;
                transform: translateY(25px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes Button_up__8EiOE {
    0% {
        opacity: 0;;
        -webkit-transform: translateY(25px);;
                transform: translateY(25px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}
.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.0rem;
}
.nav-item {
    display: flex;
    align-items: center;
    height: 80px;
    margin: 5px;
}
.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.7rem 1rem;
}
.nav-links:hover {
    background-color: #1888ff;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}
.btn{
    padding: 10px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    font-size: 18px;
    color: #fff;
    cursor: pointer;
    background-color: var(--secondary-btn-color);
}
.btn:hover{
    padding: 6px 18px;
    transition: all 0.3s ease-out;
    background-color: transparent;
    color: #fff;
    border-radius: 4px;
    border: 2px solid var(--terciary-btn-color);
}
